import React from "react";
import { useRef } from "react";
import "../assets/css/Navbar.css";
import { select } from "../assets/js/helper";

function Navbar() {
  const header = useRef(null);
  const about = useRef(null);
  const resume = useRef(null);
  const services = useRef(null);
  const portfolio = useRef(null);
  const contact = useRef(null);

  const toggleNavBar = (element) => {
    select("#navbar").classList.toggle("navbar-mobile");
    let menu = select(element);
    menu.classList.toggle("bi-list");
    menu.classList.toggle("bi-x");
  };

  const scrollToSection = (elementRef, eleId) => {
    navHandler(eleId);
    window.scrollTo({
      // top: elementRef.current.offsetTop,
      top: 0,
      behavior: "smooth",
    });
  };

  const navHandler = (eleId) => {
    let currentId = select(eleId);
    let section = select(currentId.hash);
    let navbar = select("#navbar");
    let header = select("#header");
    let sections = select("section", true);
    let navlinks = select("#navbar .nav-link", true);
    navlinks.forEach((i) => {
      i.classList.remove("active");
    });

    currentId.classList.add("active");

    if (navbar.classList.contains("navbar-mobile")) {
      navbar.classList.remove("navbar-mobile");
      let navbarToggle = select(".mobile-nav-toggle");
      navbarToggle.classList.toggle("bi-list");
      navbarToggle.classList.toggle("bi-x");
    }

    if (currentId.hash === "#header") {
      header.classList.remove("header-top");
      sections.forEach((i) => {
        i.classList.remove("section-show");
      });
      return;
    }

    if (!header.classList.contains("header-top")) {
      header.classList.add("header-top");
      setTimeout(function () {
        sections.forEach((i) => {
          i.classList.remove("section-show");
        });
        section.classList.add("section-show");
      }, 350);
    } else {
      sections.forEach((i) => {
        i.classList.remove("section-show");
      });
      section.classList.add("section-show");
    }
  };

  return (
    <div className="navCont" id="navCont">
      <nav className="navbar" id="navbar">
        <ul>
          <li onClick={() => scrollToSection(header, "#headerAct")}>
            <a href="#header" id="headerAct" className="nav-link active">
              Home
            </a>
          </li>
          <li onClick={() => scrollToSection(about, "#aboutAct")}>
            <a href="#about" id="aboutAct" className="nav-link">
              About
            </a>
          </li>
          <li onClick={() => scrollToSection(resume, "#resumeAct")}>
            <a href="#resume" id="resumeAct" className="nav-link">
              Resume
            </a>
          </li>
          <li onClick={() => scrollToSection(services, "#servicesAct")}>
            <a href="#services" id="servicesAct" className="nav-link">
              Services
            </a>
          </li>
          <li onClick={() => scrollToSection(portfolio, "#portfolioAct")}>
            <a href="#portfolio" id="portfolioAct" className="nav-link">
              Portfolio
            </a>
          </li>
          <li onClick={() => scrollToSection(contact, "#contactAct")}>
            <a href="#contact" id="contactAct" className="nav-link">
              Contact
            </a>
          </li>
        </ul>
        <i
          className="bi bi-list mobile-nav-toggle"
          id="mobile-nav-toggle-button"
          onClick={() => toggleNavBar("#mobile-nav-toggle-button")}
        ></i>
      </nav>
    </div>
  );
}

export default Navbar;
